
.game {
  display:flex;
  flex-direction: column;
  height:100vh;
  justify-content: center;
  align-items: center;
}
.game .game-info {
  margin-top:16px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.move-button {
  width: 200px;
  background-color: royalblue;
  border-width:0;
  border-radius: 5px;
  color:white;
  font-weight: 400;
  margin-bottom: 10px;
  padding: 10px 20px;
  text-decoration: none;
}
ol {
  list-style: none;
}